html,
body,
#root {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: auto;
  overflow-x: hidden;
  font-family: Titillium Web;
  font-size: 18px;
}

:root {
  --white-color: #fff;
  --black-color: #3d3c3c;
  --green-color: #2c786c;
  --green-color-80: #105749ff;
  --dark-green-color: #004445;
  --orange-color: #ffad61ff;
  --background-color-yellow: #fffef3ff;
  --background-color-grey: #f5f5f5;
  --light-green-color: #faf5e4;
  --width-350: 350px;
}

h1 {
  font-size: 2.5em;
}

.white-color {
  color: #fff !important;
  color: var(--white-color) !important;
}

.black-color {
  color: #3d3c3c !important;
  color: var(--black-color) !important;
}

.orange-color {
  color: #ffad61ff !important;
  color: var(--orange-color) !important;
}

.green-color-80 {
  color: #105749ff !important;
  color: var(--green-color-80) !important;
}

.white-color-background {
  background: #fff !important;
  background: var(--white-color) !important;
}

.green-color-80-background {
  background: #105749ff !important;
  background: var(--green-color-80) !important;
}

.green-color-80-border {
  border-color: #105749ff !important;
  border-color: var(--green-color-80) !important;
}

.width-350 {
  width: 350px;
  width: var(--width-350);
}

/* Home page styling start */
.home-banner {
  background-image: url(data:image/png;base64,);
  flex: 0 0 auto;
  width: 100%;
  display: flex;
  padding: 64px;
  align-items: center;
  border-radius: 48px;
  flex-direction: column;
  background-size: cover;
}

.home-text54 {
  color: rgba(255, 255, 255, 0.6);
  text-align: center;
}

.main-home-img {
  flex: 1 1;
  max-width: 550px;
  object-fit: cover;
  border-radius: 48px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  margin-top: 150px;
}

.rect1-home-img {
  top: 0px;
  right: -170px;
  width: 100%;
  height: 100%;
  position: absolute;
  max-width: 359px;
  max-height: 359px;
  object-fit: contain;
  object-position: center;
}

.rect2-home-img {
  right: -170px;
  bottom: 0px;
  position: absolute;
  object-fit: contain;
}
/* Home page styling end */

/* Antd styling */

.ant-layout-header {
  background: #fff !important;
  background: var(--white-color) !important;
}

.ant-layout-content {
  min-height: calc(100vh - 135px) !important;
}

.ant-menu-horizontal {
  border-bottom: none !important;
}

.ant-layout-footer {
  background: #fff !important;
  background: var(--white-color) !important;
}

.ant-menu-submenu-popup ul li span a {
  font-size: 16px;
  font-style: normal;
  font-family: Titillium Web;
  font-weight: 600;
  line-height: 1.5;
  text-transform: none;
  text-decoration: none;
  color: #105749ff !important;
  color: var(--green-color-80) !important;
}

.ant-menu-submenu-popup ul .ant-menu-item-active,
.ant-menu-submenu-popup ul .ant-menu-item-selected {
  color: #105749ff;
  color: var(--green-color-80);
  background: #f5f5f5 !important;
  background: var(--background-color-grey) !important;
}

/* Generic classes */

.d-flex {
  display: flex !important;
}

.d-contents {
  display: contents !important;
}

.d-grid {
  display: grid !important;
}

.text-align-center {
  text-align: center !important;
}

.align-self-center {
  align-self: center;
}

.m-top-10 {
  margin-top: 10% !important;
}

.m-top-5 {
  margin-top: 5% !important;
}

.m-auto {
  margin: auto !important;
}

.m-auto-lr-10-tb {
  margin: 10% auto;
}

.m-auto-tb {
  margin: auto 0;
}

.width-75 {
  width: 75% !important;
}

